@import url(https://fonts.googleapis.com/css2?family=Shojumaru&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App_App__16ZpL {
    width: 100vw;
    height: 100vh;
}
.Logo {
    width: 100vw;
    height: 50px;
    font-size: 40px;
    font-family: 'Shojumaru', cursive;
    text-align: center;
    margin: 10px 0 0 0;
}
.Eyes {
    width: 100vw;
    height: 60px;
}

.LeftEye {
    height: 30px;
    width: 30px;
    border: 3px solid #000;
    border-radius: 50%;
    background-color: #000;
    font-size: 0;
    display: inline-flex;
    margin-left: 20vw;

}

.RightEye {
    height: 30px;
    width: 30px;
    border: 3px solid #000;
    border-radius: 50%;
    background-color: #000;
    font-size: 0;
    display: inline-flex;
    justify-content:flex-start;
    margin-left: 40vw;

}
.Mouth {
    height: 240px;
    width: 60%;
    border: 30px solid #000;
    border-radius: 50%;
    margin: 0 auto;
    z-index: 1;
}

.MouthMunching {
    animation: munchingMouth 1s;
    animation-iteration-count: 10;
}

@keyframes munchingMouth {
    0% {height: 240px}
    50% {height: 120px}
    100% {height: 240px}
}

.UpperTeethWrapper {
    height: 120px;
    display: flex;
    justify-content: center;
}

.UpperTooth {
    height: 20px;
    width: 20px;
    border: 3px solid #000;
    margin: 0 1px;
    border-radius: 30px 30px 5px 5px;
    margin-bottom: 25px;
    position: relative;
    z-index: -1;

}
.LowerTeethWrapper {
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.LowerTooth {
    height: 20px;
    width: 20px;
    border: 3px solid #000;
    margin: 0 1px;
    border-radius: 5px 5px 30px 30px;
    margin-top: 25px;
    z-index: -1;
}

.LowerMunching {
    animation: munchingLower 1s;
    animation-iteration-count: 10;
}

@keyframes munchingLower {
    0% {transform: translateY(0)}
    50% {transform: translateY(-120px)}
    100% {transform: translateY(0)}
}

.Button {
    background: #000000;
    color: white;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
    font-size: 24px;
}

.ButtonDisabled {
    background: #000000;
    color: white;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
    font-size: 24px;
    cursor: not-allowed;
}

.Form {
    display: flex;
    justify-content: center;
    height: 50px;
}

.Form input {
    font-size: 16px;
}

.DefaultText {
    font-size: 20px;
    display: flex;
    justify-content: center;
}

.GuessSucess {
    font-size: 20px;
    margin: 10px 30px;
    display: flex;
    justify-content: center;
    color: green;
}

.GuessInvalid {
    font-size: 20px;
    display: flex;
    justify-content: center;
    color: red;
}

.ShowFactsBtn {
    display: flex;
    justify-content: center;
}

.ShowFactsBtn button {
    height: 40px;
    border: 2px solid #01c177;
    border-radius: 10px;
    background: none;
    font-size: 14px;
}

.ShowFactsBtn button:hover {
    color: #fff;
    background-color: #01c177;
    cursor: pointer;
}



#factsAboutMenu {
    height: 100%;
    width: 80vw;
    margin: 50px auto;
    border: 2px solid #01c177;
    border-radius: 20px;
    background-color: #01c177;
}

.ListOfFacts {
    width: 80%;
    list-style-type: none;
    font-size: 20px;
    text-align: left;
    padding: 0;
    color: #fff;
    margin: 0;
    padding: 25px 25px 0 25px ;
}

.ListOfFacts li {
    padding-bottom: 10px;
}
.MouthWrapper {
    height: 300px;
    width: 100vw;

}

.UpperTeethWrapper, .StartMunchingButton {
    height: 120px;
    display: flex;
    justify-content: center;
}


.StartMunchingButton {
    width: 124px;
    height: 80px;
    margin: 20px auto;
    padding: 0;
}


